<template>
  <div class="popUps" :class="{backColor: isAddClass}" v-show="isShow">
    <!--  -->
    <popSlot :name="'new'" :title="''" :prop="'newCanvas'">
      <div class="recordWrapper setCanvas">
        <div class="newCanvasInput">
          <div class="inputOne">
            <span></span>
            <input type="text" v-model="canvasObj.name">
          </div>
          <div>
            <span></span>
            <div class="spinner">
              <input class="spinnerInput" v-model="canvasObj.height">
              <div class="spinnerArrow">
                <div class="spinnerArrowUp" @click="changeInput('height', 'add')"></div>
                <div class="spinnerArrowDown" @click="changeInput('height', 'down')"></div>
              </div>
            </div>
            <span></span>
          </div>
          <div>
            <span></span>
            <div class="spinner">
              <input class="spinnerInput" v-model="canvasObj.width">
              <div class="spinnerArrow">
                <div class="spinnerArrowUp" @click="changeInput('width', 'add')"></div>
                <div class="spinnerArrowDown" @click="changeInput('width', 'down')"></div>
              </div>
            </div>
            <span></span>
          </div>
        </div>
        <div class="newCanvasButton">
          <buttonIcon :type="'closeSecond'" :callback="close" :prop="'newCanvas'"></buttonIcon>
          <buttonIcon :type="'defineSecond'" :callback="add"></buttonIcon>
        </div>
      </div>
    </popSlot>
    <!--  -->
    <popSlot :title="''" :prop="'closeCanvas'">
      <div class="recordWrapper closeCanvas">
        <div class="closeCanvasTle">
          <span class="closeCanvasIcon"></span>
          <span class="closeCanvasCon"></span>
        </div>
        <div class="closeCanvasBtn">
          <buttonIcon :type="'defineFirst'" :callback="popCanvas"></buttonIcon>
          <buttonIcon :type="'closeFirst'" :callback="close" :prop="'closeCanvas'"></buttonIcon>
        </div>
      </div>
    </popSlot>
    <!--  -->
    <popSlot :title="''" :prop="'clearCanvas'">
      <div class="recordWrapper closeCanvas">
        <div class="closeCanvasTle">
          <span class="closeCanvasIcon"></span>
          <span class="closeCanvasCon"></span>
        </div>
        <div class="closeCanvasBtn">
          <buttonIcon :type="'defineFirst'" :callback="clearCanvas"></buttonIcon>
          <buttonIcon :type="'closeFirst'" :callback="close" :prop="'clearCanvas'"></buttonIcon>
        </div>
      </div>
    </popSlot>
    <!-- / -->
    <fieldSet :name="'light'"></fieldSet>
    <!-- / -->
    <fieldSet :name="'colorpalettes'"></fieldSet>
    <!-- WebPhotoshop -->
    <popSlot :name="'about'" :title="''" :prop="'aboutWebPhotoshop'">
      <div class="recordWrapper aboutWebPhotoshop">
        <div align="center">
          <img src="../../src/assets/icons/log.png" />
          <span style="font-size: 30px;">WebPhotoshop</span>
        </div>
        <hr style="border: 0; background-color: #95B8E7; height: 1px;" />
        <div>
          <p>WebPhotoshop</p>
          <p>1.0()</p>
          <p>2018-04-19</p>
          <p>Create by </p>
          <p>QQ: 2804673379</p>
        </div>
        <div class="btn">
          <button @click="popUpsKey.aboutWebPhotoshop=false"></button>
        </div>
      </div>
    </popSlot>
    <!--  -->
    <colorCurve></colorCurve>
    <!--  -->
    <colorLevel></colorLevel>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import buttonIcon from './selectmodules/buttonIcon.vue'
import fieldSet from './selectmodules/fieldSet.vue'
import popSlot from './popSlot.vue'
import colorCurve from './colorCurve.vue'
import colorLevel from './colorLevel.vue'
export default {
  name: 'pop-ups',
  components: {
    buttonIcon,
    fieldSet,
    popSlot,
    colorCurve,
    colorLevel
  },
  data () {
    return {
      canvasObj: {
        name: '',
        width: 820,
        height: 520,
        context: '',
        canvas: '',
        imgData: '',
        scaleProportion: 1,
        dataArr: [{
          id: 24,
          imgData: ''
        }],
        index: 0,
        lightObj: {
          name: 'light',
          title: '/',
          data: [{
            title: '',
            num: 0,
            len: [-50, 50]
          }, {
            title: '',
            num: 0,
            len: [-50, 50]
          }]
        }
      }
    }
  },
  computed: {
    ...mapState([
      'canvasArr',
      'popUpsKey',
      'nowCanvas',
      'selectGrayscale'
    ]),
    isAddClass () {
      if (this.popUpsKey.light || this.popUpsKey.colorpalettes || this.popUpsKey.colorCurve || this.popUpsKey.colorLevel) {
        return false
      } else {
        return true
      }
    },
    isShow () {
      var a = false
      for (const key in this.popUpsKey) {
        if (this.popUpsKey[key] && key !== 'colorPicker') {
          a = true
          break
        }
      }
      return a
    }
  },
  methods: {
    close (prop) {
      this.popUpsKey[prop] = false
    },
    add () {
      this.$store.commit('addCanvasArr', JSON.parse(JSON.stringify(this.canvasObj)))
      this.close('newCanvas')
    },
    changeInput (prop, key) {
      if (key === 'add') {
        this.canvasObj[prop]++
      } else {
        this.canvasObj[prop]--
      }
    },
    popCanvas () {
      this.$store.commit('popCanvasArr', this.nowCanvas)
      this.close('closeCanvas')
    },
    // 
    clearCanvas () {
      this.$store.commit('clearCanvas')
      this.close('clearCanvas')
    }
  }
}
</script>

<style lang="scss">
.backColor {
  background-color: rgba(204, 204, 204, 0.4);
}
.popUps {
  position: absolute;
  z-index: 100;
  width: 100%;
  height: 100%;
  top: 0px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  // 
  .setCanvas {
    width: 286px;
    height: 165px;
    .newCanvasInput {
      height: 130px;
      &>div {
        display: flex;
        align-items: center;
        padding: 5px 0 5px 45px;
        span {
          font-size: 13px;
          padding-left: 5px;
        }
        .spinner {
          border: 1px solid #95B8E7;
          display: flex;
          align-items: center;
          .spinnerInput {
            border: none;
            padding: 0 2px;
            width: 56px;
            height: 20px;
            outline: none;
          }
          .spinnerArrow {
            width: 18px;
            height: 20px;
            background-color: #E0ECFF;
            div {
              height: 10px;
            }
            .spinnerArrowUp {
              background: url('../../src/assets/default/spinner_arrows.png') no-repeat 1px center;
            }
            .spinnerArrowDown {
              background: url('../../src/assets/default/spinner_arrows.png') no-repeat -15px center;
            }
          }
        }
      }
      .inputOne {
        input {
          border: 1px solid #95B8E7;
        }
      }
    }
    .newCanvasButton {
      height: 34px;
      border-top: 1px solid #CBC6C6;
      background: #F4F4F4;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      &>div {
        padding-right: 5px;
      }
    }
  }
  .closeCanvas {
    width: 266px;
    height: 76px;
    padding: 10px;
    .closeCanvasTle {
      .closeCanvasIcon {
        display: inline-block;
        width: 32px;
        height: 32px;
        background: url('../../src/assets/default/messager_icons.png') no-repeat scroll -32px 0;
      }
      .closeCanvasCon {
        vertical-align: top;
        font-size: 13px;
        padding-left: 5px;
      }
    }
    .closeCanvasBtn {
      margin-top: 17px;
      display: flex;
      justify-content: center;
      &>div {
        padding: 0 5px;
      }
    }
  }
  // WebPhotoshp
  .aboutWebPhotoshop {
    width: 570px;
    height: 350px;
    padding: 8px;
    p {
      font-size: medium;
    }
    .btn {
      position: absolute;
      right: 0;
      bottom: 30px;
      button {
        float: right;
        margin-right: 40px;
        width: 80px;
        height: 26px;
        border: 1px solid #95B8E7;
      }
    }
  }
}
</style>



